(function () {
    'use strict';
    var angular = require('angular');
    require('./submitted-application.component.scss');

    var submittedAppln = angular.module('app').component('submittedAppln', {
        template: require('./submitted-application.component.html'),
        controller: ['$anchorScroll', 'analyticService', 'appState', function ($anchorScroll, analyticService, appState) {
            var vm = this;

            vm.$onInit = function () {
                analyticService.trackPageLoad(analyticService.pageList.submittedApplication);
                appState.clearAll();
                $anchorScroll();
            };

            vm.exit = function () {
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = submittedAppln.name;
})();



